@import "src/styles/Mixins";
@import "src/styles/Variables";

.advertisingBlockContainer {
  width: 100%;
  background-color: white;
  padding: 24.75px 0 40.75px 0;
  cursor: default;


  .advertisingBlock {
    display: flex;
    flex-direction: row;
    overflow: hidden;
  }
}

.mainTitle {
  padding-top: 42.25px;
  margin-bottom: 32px;
  max-width: 534px;
  @include SFDisplayBold();
  font-size: 42px;
  line-height: 120%;
}

.subTitleWrapper {
  display: flex;

  & p {
    max-width: 254px;
    @include SFTextRegular();
    font-size: 16px;
    line-height: 140%;
    color: $textColor;
  }

  & span {
    @include SFDisplayBold();
    font-size: 16px;
    line-height: 130%;
    color: $textColor;
  }

  margin-bottom: 33px;
}

.subTitleOne {
  display: flex;
}

.subTitleOne:before {
  content: '';
  display: block;
  width: 2px;
  height: 42px;
  background: #98C6CD;
  margin-right: 8px;
}

.subTitleTwo {
  display: flex;
}

.subTitleTwo:before {
  content: '';
  display: block;
  width: 2px;
  height: 42px;
  background: #98C6CD;
  margin-right: 8px;
}

.mainImage {
  min-width: 573px;
  height: 377px;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@media (max-width: 992px) {
  .mainTitle {
    width: 267px;
    font-size: 21px;
    line-height: 28px;
    color: $textColor;
    padding-top: 0 !important;
  }
  .subTitleWrapper {
    flex-direction: column;
    font-size: 15px;
    line-height: 140%;
  }
  .subTitleOne {
    margin-bottom: 16px;
  }
  .advertisingBlock {
    overflow: hidden;
  }
  .subTitleWrapper {
    & p {
    }

    font-size: 15px;
    max-width: 183px;
  }
}

@media (max-width: 768px) {
  .mainWrapper {
    & button {
      width: 137px;
    }
  }
}